import React, { Component } from 'react';
import LeftCover from "../common/LeftCover";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Inputs from "../common/InputFields";
import { Login } from "../../redux/actions";
import { InputValidate } from "../../redux/actions/Inputs";
class LOGIN extends Component {
    constructor(props) {
        console.log(Login);
        super(props);
        this.state = {
            email: null,
            password: null,
            errors: {
                email: null,
                password: null
            },
            passIcon: "fa-eye"
        };
    }
    setStateHandler = val => {
        this.setState({
            errors: val.errors,
            [val.name]: val[val.name]
        })
    }
    passwordHandler = event => {
        const classContain = event.target.className.split(" ")[1];
        if (classContain === "fa-eye") {
            this.setState({
                passIcon: "fa-eye-slash"
            })
        }
        else {
            this.setState({
                passIcon: "fa-eye"
            })
        }
    }
    HandleChanging = e => {
        let errors = InputValidate(e, this.state);
        this.setState(errors);
    }
    onLoginHandler = e => {
        console.log(this.props)
        e.preventDefault();
        const { email, password } = this.state;
        const login = email;
        if (this.state.errors.email === "" && this.state.errors.password === "") {
            this.props.LOGIN({ login, password });
        }
    }
    render() {
        console.log(this.state)
        return (
            <React.Fragment>
                <div className="container-fluid Login">
                    <div className="row">
                        <LeftCover />
                        <div className="col-md-6 col-12 loginForm">
                            <div className="content">
                                <img src="./img/logo.png" className="logo" alt="" />
                                <form onSubmit={this.onLoginHandler}>
                                    <Inputs label="E-mail" name="email" types="email" state={this.state} stateHandler={this.setStateHandler} />
                                    {this.state.errors.email !== "" ? <span className="error text-danger">{this.state.errors.email}</span> : ""}
                                    <div className="inputs">
                                        <label htmlFor="password">Password</label>
                                        <div className="input-group">
                                            <input type={this.state.passIcon === "fa-eye" ? "password" : "text"} name="password" id="password" placeholder="Enter Your Password Here" onChange={this.HandleChanging} />
                                            <i className={`fas ${this.state.passIcon}`} onClick={this.passwordHandler}></i>
                                        </div>
                                    </div>
                                    {this.state.errors.password !== "" ? <span className="error text-danger">{this.state.errors.password}</span> : ""}
                                    <button onClick={this.onLoginHandler}>Sign In</button>
                                </form>
                                <span className="forgetPassword">Forget your password?</span>
                                <span className="register">Don't have an account? <u><Link to="/signup">Register</Link></u></span>
                                <div className="socialLogin">
                                    <div className="text-Content">
                                        <div className="icon">
                                            <i className="fab fa-twitter"></i>
                                        </div>
                                        <div className="Text">
                                            <span>Twitter</span>
                                        </div>
                                    </div>
                                    <div className="text-Content">
                                        <div className="icon">
                                            <i className="fab fa-facebook-f" style={{ padding: "6px 9px" }}></i>
                                        </div>
                                        <div className="Text">
                                            <span>Facebook</span>
                                        </div>
                                    </div>
                                    <div className="text-Content">
                                        <div className="icon">
                                            <i className="fab fa-google-plus-g"></i>
                                        </div>
                                        <div className="Text">
                                            <span>Google</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
const mapStateToProps = state => {
    return {
        ...state
    }
}
const mapDispatchToProps = dispatch => {
    return {
        LOGIN: e => {
            dispatch(Login.ON_LOGIN(e));
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(LOGIN);